@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700&display=swap);
html {
	box-sizing: border-box;
	font-size: 62.5%;
}

*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	margin: 0;
	padding: 0;
	background-color: #86cde8;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
		'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
		'Helvetica Neue', sans-serif, 'Lato';
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.i-ml {
	margin-left: 10px;
}

.quote-box {
	position: relative;
	padding: 2rem;
	background-color: #fff;
	box-shadow: 0rem 0rem 0.6rem 0.2rem rgba(0, 0, 0, 0.3);
}

.quote-box__inner {
	padding: 110px 65px;
}

.quote-box__buttons {
	position: absolute;
	top: 24px;
	right: -30px;
	display: flex;
	align-items: center;
	padding: 16px 16px 16px 6px;
	background-color: #02a7e6;
}

.quote {
}

.quote__text {
	position: relative;
	max-width: 45.5rem;
}

.quote__text::before {
	content: '\201C';
	position: absolute;
	top: -130px;
	left: -65px;
	z-index: 0;
	font-size: 35rem;
	color: #f3f2f2;
	line-height: 1;
}
.quote__text .text {
	position: relative;
	z-index: 1;
}

.quote__author {
	position: relative;
	margin-top: 20px;
	padding-left: 20px;
}

.quote__author::before {
	content: '\2212';
	position: absolute;
	top: 50%;
	left: 0;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	color: #d2d2d2;
	font-size: 2.5rem;
}

.text {
	color: #393c62;
	font-size: 2.8rem;
}

.author {
	font-size: 2rem;
	font-style: italic;
	color: #d2d2d2;
}

.share-btn {
	padding: 8px 14px;
	background-color: #fff;
	font-size: 1.6rem;
	font-weight: bold;
	text-decoration: none;
}

.share-btn--twitter {
	color: #02a7e6;
	-webkit-transition: -webkit-transform 0.3s ease-in-out;
	transition: -webkit-transform 0.3s ease-in-out;
	transition: transform 0.3s ease-in-out;
	transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.share-btn--twitter:hover {
	-webkit-transform: translateY(10px);
	        transform: translateY(10px);
}

.button {
	padding: 8px 6px;
	border: 0;
	font-size: 1.4rem;
	font-family: 'Lato';
	font-weight: bold;
	cursor: pointer;
}

.button:focus {
	outline: none;
}

.button--blue {
	background-color: #58c5f0;
	color: #fff;
	-webkit-transition: -webkit-transform 0.3s ease-in-out;
	transition: -webkit-transform 0.3s ease-in-out;
	transition: transform 0.3s ease-in-out;
	transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.button--blue:hover {
	-webkit-transform: translateY(-10px);
	        transform: translateY(-10px);
}

